@import "../../global.scss";

.topbar {
  background-color: white;
  color: $mainColor;

  width: 100%;
  height: 70px;

  position: fixed;
  top: 0;

  z-index: 3;

  transition: all 1s ease;

  .wrapper {
    padding: 10px 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 5px;

    .left {
      display: flex;
      align-items: center;

      .logo {
        text-decoration: none;
        color: inherit;

        font-size: 30px;
        font-weight: 700;

        margin-right: 30px;

        @include mobile {
          font-size: 20px;
        }
      }

      .itemContainer {
        display: flex;
        align-items: center;
        margin-left: 10px;

        @include mobile {
          display: none;
        }

        a {
          color: inherit;
        }

        .icon {
          font-size: 200%;
        }

        span {
          font-size: 15px;
          font-weight: 400;
        }
      }
    }

    .right {
      .hamburger {
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        cursor: pointer;

        width: 32px;
        height: 25px;

        overflow: hidden;

        span {
          width: 100%;
          height: 3px;
          background-color: blue;

          transform-origin: left;

          transition: all 1s ease;
        }
      }
    }
  }

  &.active {
    background-color: $bg-red;
    color: white;

    .hamburger {
      span {
        &:nth-child(2) {
          opacity: 0;
        }
        &:first-child {
          background-color: white;
          transform: rotate(45deg);
        }
        &:last-child {
          background-color: white;
          transform: rotate(-45deg);
        }
      }
    }

    &.dark {
      background-color: #347d39;
      color: $white;

      .hamburger {
        span {
          &:nth-child(2) {
            opacity: 0;
          }
          &:first-child {
            background-color: white;
            transform: rotate(45deg);
          }
          &:last-child {
            background-color: white;
            transform: rotate(-45deg);
          }
        }
      }
    }
  }

  &.dark {
    background-color: #22272e;
    color: $white;

    .left {
      .itemContainer {
        a {
          color: white;
        }
      }
    }

    .right {
      .hamburger {
        span {
          background-color: $white;
          color: white;
        }
      }
    }
  }
}
