@import "../../global.scss";

.menu {
  width: 300px;
  height: 100vh;

  background-color: $bg-red;

  position: fixed;
  top: 0;
  right: -300px;

  z-index: 2;

  display: flex;
  align-items: center;
  justify-content: center;

  flex-direction: column;

  transition: all 1s ease;

  &.active {
    right: 0;
  }

  ul {
    margin: 0;
    padding: 0;

    list-style: none;

    font-size: 30px;
    font-weight: 300;

    color: white;

    width: 80%;

    li {
      margin-bottom: 25px;
      a {
        font-size: inherit;

        color: inherit;

        text-decoration: none;
      }

      &:hover {
        font-weight: 500;
      }
    }
  }

  &.dark {
    background-color: #347d39;

    ul {
      color: white;
      li {
        margin-bottom: 25px;
        a {
          color: inherit;
        }
      }
    }
  }
}
