@import "../../global.scss";

.projectList {
  font-size: 14px;
  margin-right: 50px;
  padding: 7px;
  border-radius: 10px;
  cursor: pointer;

  color: #ffdc00;
  background-color: $fill-maroon;

  @include mobile {
    margin-right: 20px;
  }

  &.active {
    background-color: #ff4136;

    &.dark {
      background-color: #347d39;
    }
  }
}
