@import "../../global.scss";

.certificates {
  background-image: url("https://bs-uploads.toptal.io/blackfish-uploads/blog/post/seo/og_image_file/og_image/17844/ux-designer-portfolios-59442fc10a13700c71c7e7f0951416fe.png");
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;

  display: flex;
  align-items: center;
  justify-content: center;

  position: relative;

  &.dark {
    background-image: url("https://i.imgur.com/nvipteM.png");

    .arrow {
      &:hover {
        color: $bg-maroon;
      }
    }

    .slider {
      .container {
        .item {
          color: $bg-gray;
          background-color: #22272e;
        }
      }
    }
  }

  .arrow {
    position: absolute;
    font-size: 10vh;
    cursor: pointer;

    @include mobile {
      display: none;
    }

    &:hover {
      color: white;
    }

    &.left {
      left: 2vw;
    }

    &.right {
      right: 2vw;
    }
  }

  .slider {
    height: 350px;

    display: flex;

    position: absolute;
    left: 0;
    transition: all 1s ease-out;

    @include mobile {
      height: 100vh;
      flex-direction: column;
      justify-content: center;
    }

    .container {
      width: 100vw;
      display: flex;
      align-items: center;
      justify-content: center;

      .item {
        width: 700px;
        height: 100%;

        background-color: white;

        border-radius: 20px 0 0 20px;

        display: flex;
        align-items: center;
        justify-content: center;

        overflow-y: hidden;

        @include mobile {
          width: 90%;
          height: 150px;

          margin: 5px 0;
        }

        .left {
          flex: 2;
          height: 80%;

          display: flex;
          align-items: center;
          justify-content: center;

          .leftContainer {
            width: 90%;
            height: 90%;

            display: flex;
            flex-direction: column;
            justify-content: space-around;

            a {
              color: #0074d9;
            }

            h2 {
              font-size: 20px;
              @include mobile {
                font-size: 12px;
              }
            }
          }
        }

        .right {
          flex: 4;
          height: 100%;

          display: flex;
          align-items: center;
          justify-content: left;

          overflow-y: hidden;

          img {
            padding: 5px 5px;
            width: 100%;
            height: auto;
          }
        }
      }
    }
  }
}
