@import "../../global.scss";

.skills {
  background-image: url("https://bs-uploads.toptal.io/blackfish-uploads/blog/post/seo/og_image_file/og_image/17844/ux-designer-portfolios-59442fc10a13700c71c7e7f0951416fe.png");
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;

  display: flex;
  align-items: center;
  justify-content: center;

  position: relative;

  &.dark {
    background-image: url("https://i.imgur.com/nvipteM.png");

    .arrow {
      &:hover {
        color: #cf6679;
      }
    }

    .slider {
      .container {
        .item {
          color: $bg-gray;
          background-color: #22272e;
          .left {
            .leftContainer {
              .imgContainer {
                background-color: #cf6679;
              }
            }
          }
        }
      }
    }
  }

  .arrow {
    position: absolute;
    font-size: 10vh;
    cursor: pointer;

    @include mobile {
      display: none;
    }

    &:hover {
      color: white;
    }

    &.left {
      left: 2vw;
    }

    &.right {
      right: 2vw;
    }
  }

  .slider {
    height: 350px;

    display: flex;

    position: absolute;
    left: 0;
    transition: all 1s ease-out;

    @include mobile {
      height: 100vh;
      flex-direction: column;
      justify-content: center;
    }

    .container {
      width: 100vw;
      display: flex;
      align-items: center;
      justify-content: center;

      .item {
        width: 700px;
        height: 100%;

        background-color: white;

        border-radius: 20px;

        display: flex;
        align-items: center;
        justify-content: center;

        @include mobile {
          width: 90%;
          height: 150px;

          margin: 5px 0;
        }

        .left {
          flex: 4;
          height: 80%;

          display: flex;
          align-items: center;
          justify-content: center;

          .leftContainer {
            width: 90%;
            height: 90%;

            display: flex;
            flex-direction: column;
            justify-content: space-between;

            a {
              color: #0074d9;
            }

            .imgContainer {
              width: 40px;
              height: 40px;

              border-radius: 50%;
              background-color: lightcoral;

              display: flex;
              align-items: center;
              justify-content: center;

              @include mobile {
                width: 30px;
                height: 30px;
              }

              img {
                width: 25px;
              }
            }

            h2 {
              font-size: 20px;
              @include mobile {
                font-size: 12px;
              }
            }

            p {
              overflow: hidden;
              font-size: 13px;

              @include mobile {
                display: none;
              }
            }

            span {
              font-size: 12px;
              font-weight: 600;
              text-decoration: underline;
              cursor: pointer;
            }
          }
        }

        .right {
          flex: 8;
          height: 100%;

          display: flex;
          align-items: center;
          justify-content: center;

          padding-right: 10px;

          img {
            width: 100%;
            height: auto;

            border: 2px solid black;
          }
        }
      }
    }
  }
}
