@import "../../global.scss";

.intro {
  color: $bg-white;

  background-image: url("https://bs-uploads.toptal.io/blackfish-uploads/blog/post/seo/og_image_file/og_image/17844/ux-designer-portfolios-59442fc10a13700c71c7e7f0951416fe.png");
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;

  &.dark {
    background-image: url("https://i.imgur.com/nvipteM.png");

    .left {
      .imgContainer {
        background-color: #adbac7;
      }
    }

    .right {
      .wrapper {
        h2,
        h1 {
          color: $bg-gray;

          &.text-effect {
            span {
              color: $bg-green;
            }
          }
        }
      }

      a {
        .icon {
          color: #dddddd;
        }
      }
    }
  }

  display: flex;

  @include mobile {
    flex-direction: column;
    align-items: center;
  }

  .left {
    flex: 1;
    overflow: hidden;

    display: flex;
    justify-content: center;
    align-items: center;

    .imgContainer {
      width: 600px;
      height: 600px;
      background-color: #7fdbff;

      border-radius: 50%;

      display: flex;
      align-items: flex-end;
      justify-content: center;

      float: right;

      @include mobile {
        align-items: flex-start;
      }

      img {
        height: 100%;

        @include mobile {
          height: 50vh;
        }
      }
    }

    @include mobile {
      display: none;
    }
  }

  .right {
    flex: 1;

    position: relative;

    padding-left: 5px;

    .wrapper {
      width: 100%;
      height: 100%;

      margin-top: 20%;

      display: flex;
      flex-direction: column;

      justify-content: center;

      @include mobile {
        padding-left: 0;

        align-items: center;
      }

      h1 {
        font-size: 60px;
        margin: 10px 0;

        &.text-effect {
          font-size: 20px;

          span {
            font-size: 40px;
            color: $bg-red;

            @include mobile {
              font-size: 30px;
            }
          }

          .ityped-cursor {
            animation: blink 1s infinite;
          }

          @keyframes blink {
            50% {
              opacity: 1;
            }

            100% {
              opacity: 0;
            }
          }
        }

        @include mobile {
          font-size: 40px;
          text-align: center;
        }
      }

      h2 {
        font-size: 35px;

        @include mobile {
          font-size: 30px;
        }
      }
    }

    a {
      position: absolute;
      bottom: 10px;
      left: 40%;

      .icon {
        font-size: 100px;
        width: 30%;
        color: $bg-red;

        animation: arrowBlink 3s infinite;
      }

      @keyframes arrowBlink {
        100% {
          opacity: 0;
        }
      }
    }
  }
}
