@import "../../global.scss";

.project {
  background-image: url("https://bs-uploads.toptal.io/blackfish-uploads/blog/post/seo/og_image_file/og_image/17844/ux-designer-portfolios-59442fc10a13700c71c7e7f0951416fe.png");
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;

  color: white;

  display: flex;
  align-items: center;

  flex-direction: column;

  &.dark {
    background-image: url("https://i.imgur.com/nvipteM.png");
    color: $bg-gray;

    .container {
      .item {
        color: #347d39;
      }
    }
  }

  h1 {
    font-size: 50px;

    @include mobile {
      font-size: 20px;
    }
  }

  ul {
    margin: 10px;
    padding: 0;

    list-style: none;

    display: flex;

    @include mobile {
      flex-wrap: wrap;
      justify-content: center;
    }
  }

  .container {
    width: 70%;

    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;

    overflow: hidden;

    @include mobile {
      width: 100%;
    }

    .item {
      width: 350px;
      height: 200px;

      border: 1px solid rgb(240, 239, 239);
      margin: 10px 20px;
      display: flex;
      align-items: center;
      justify-content: center;
      color: white;
      position: relative;
      transition: all 0.5s ease;

      &:hover {
        background-color: inherit;
        color: inherit;
        img {
          opacity: 0.7;
          z-index: 0;
        }
      }

      @include mobile {
        width: 130px;
        height: 100px;
      }

      h3 {
        position: absolute;
        font-size: 20px;
      }

      img {
        width: 100%;
        height: 100%;

        z-index: 1;

        background-repeat: no-repeat;
        background-attachment: fixed;
        background-size: cover;
      }
    }
  }
}
