@import "./global.scss";

.app {
  height: 100vh;

  .toggle {
    z-index: 100;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    position: fixed;
    left: 83vw;
    top: 3vh;

    transform: scale(2);

    cursor: pointer;

    @include mobile {
      display: none;
    }

    .off {
      color: $bg-blue;
    }

    .on {
      color: $bg-white;
    }
  }

  .sections {
    width: 100%;
    height: calc(100vh - 70px);

    scrollbar-width: none;
    &::-webkit-scrollbar {
      display: none;
    }

    position: relative;
    top: 70px;

    scroll-snap-type: y mandatory;
    scroll-behavior: smooth;

    > * {
      width: 100%;
      height: calc(100vh - 70px);
      scroll-snap-align: start;
    }
  }
}
